.about-header {
    display: grid;
    grid-template-columns: 1fr min-content;
    grid-template-rows: min-content min-content;
    position: relative;
    &__top {
        grid-column: 1 / 3;
        display: flex;
        justify-content: space-between;
        background: rgba(245, 245, 250, 1);
        margin: 0 -15px;
        padding-left: 15px;
        padding-right: 15px;
        height: 45px;
        align-items: center;
    }
    &__lang {
        display: flex;
        list-style: none;
        padding-left: 0;
        button {
            font-size: 12px;
            line-height: 15px;
            text-align: center;
            letter-spacing: 1px;
            color: #b3b3ba;
            font-family: "Montserrat";
            font-style: normal;
            font-weight: 700;
            text-transform: uppercase;
            margin-right: 14px;
        }
    }
    &__address {
        display: none;
    }
    &__fb {
        display: none;
    }
    &__pages {
        display: none;
        position: absolute;
        background-color: white;
        width: 100%;
        right: 0;
        top: 100px;
        list-style: none;
        padding-left: 0;
        text-decoration: none;
        height: calc(100vh - 100px);
        overflow: auto;
        padding: 12px;
        scrollbar-width: none;
        -ms-overflow-style: none;
        li {
            text-align: center;
            background-color: $greybg;
            border-radius: 12px;
            padding-top: 7px;
            padding-bottom: 7px;
            a {
                text-decoration: none;
                font-size: 24px;
                line-height: 29px;
                color: $colorblue;
            }
        }
        li:not(:last-child) {
            margin-bottom: 15px;
        }
    }
    &__pages::-webkit-scrollbar {
        width: 0;
        height: 0;
    }
    &__pages a:hover {
        color: $hover;
    }
    &__pages li:hover {
        background-color: #e6e6f0;
        cursor: pointer;
    }

    &__pages.active {
        display: block;
    }

    &__burger {
        background-image: url(../images/Hamburger-about.svg);
        width: 32px;
        height: 32px;
        background-color: transparent;
        margin-top: 13px;
    }
    &__burger.close {
        background-image: url(../images/Close.svg);
        background-color: #ffffff;
        width: 32px;
        height: 32px;
        padding: 0;
    }
    &__logo {
        display: flex;
        align-items: center;
        margin-top: 15px;
        margin-bottom: 15px;
    }
    &__tel {
        font-size: 16px;
        line-height: 20px;
        color: #b3b3ba;
        text-decoration: none;
    }
    &__logo-text {
        font-weight: 400;
        font-size: 20px;
        line-height: 24px;
        color: #323264;
        align-self: flex-start;
    }
    &__logo-text::before {
        content: "";
        background-image: url(../images/Slash.png);
        width: 12px;
        height: 26px;
        display: inline-flex;
    }
    @media (min-width: 768px) {
    }
}
